@font-face {
    font-family: 'Magnat Head';
    src: url('/assets/fonts/subset-Magnat-HeadMedium.woff2') format('woff2'),
        url('/assets/fonts/subset-Magnat-HeadMedium.woff') format('woff');
    font-weight: 500;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Gaultier';
    src: url('/assets/fonts/subset-Gaultier-Regular.woff2') format('woff2'),
        url('/assets/fonts/subset-Gaultier-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Gaultier';
    src: url('/assets/fonts/subset-Gaultier-Bold.woff2') format('woff2'),
        url('/assets/fonts/subset-Gaultier-Bold.woff') format('woff');
    font-weight: bold;
    font-style: normal;
    font-display: swap;
}

:root {
    --color-3: #145d55;
    font-size: 24px;
}

html {
    font-family: 'Gaultier', 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', 'SongTi TC', sans-serif;

    background: #fff;
    color: #424242;
}

main {
    max-width: 100%;
    padding-left: 0;
    padding-right: 0;

    section + section {
        margin-top: 0;
    }
}

header, h1, h2, h3 {
    text-align: left;
}

h1 {
    font-family: 'Magnat Head', 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', 'SongTi TC', sans-serif;
    font-size: 2em;
    font-weight: 500;
    text-transform: none;
    letter-spacing: 0.02em;
}

h2, h3 {
    font-family: 'gaultier', 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', 'SongTi TC', sans-serif;
    font-size: 1em;
    font-weight: bold;
    font-style: normal;
    text-transform: none;
}

header, main section {
    padding: 2rem 2rem;
}

main p {
    max-width: 40em;
}

p + img {
    margin-top: 2em;
}

#logo {
    background: no-repeat url('/assets/images/kajo-background-1.jpg');
    background-color: #145d55;
    color: #fff;
}

#kajo-logo {
    margin-top: 3rem;
    margin-bottom: 3rem;
}

@media (min-width: 600px) {
    header, main section {
        padding: 2rem 5rem;
    }

    #kajo-logo {
        max-width: 15rem;
    }
}

@media (min-width: 1200px) {
    #logo {
        display: grid;
    
        grid-template-areas:
        "header ."
        "logo logo-text";
    
        grid-template-columns: 15rem 1fr;

        grid-column-gap: 4em;
    }
    
    #logo h2 {
        grid-area: header;
    }
    
    #kajo-logo {
        grid-area: logo;
        margin-top: 2em;
        margin-bottom: 0;
    }

    #logo div {
        grid-area: logo-text;
    }
}